.work-experiences-list {
    margin-top: 24px;
    margin-bottom: 48px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

.work-item {
    --work-item-width: 80vw;
    --horizontal-margins: 24px;
    --work-item-vertical-content-spacing: .5vmin;
 }
 
 .work-item {
     min-width: var(--work-item-width);
     max-width: var(--work-item-width);
     margin-top: 48px;
     margin-left: var(--horizontal-margins);
     margin-right: var(--horizontal-margins);
     display: flex;
     flex-direction: row;
     align-items: center;
     justify-content: flex-start;
     color: #c2c1c0;
 }
 
 .work-item-text {
     padding-bottom: 4px;
     margin-left: calc(10px + 1vmin);
     display: flex;
     flex-direction: column;
     align-items: flex-start;
     justify-content: space-between;
 }
 
 .work-item-title {
   font-size: calc(10px + 2vmin);
   color: white;
 }
 
 .work-item-description {
     flex: 1;
     margin-top: var(--work-item-vertical-content-spacing);
     font-size: calc(6px + 1.5vmin);
 }
 
 .work-item-skills {
     margin-top: var(--work-item-vertical-content-spacing);
     font-size: calc(2px + 2vmin);
     font-style: italic;
     color: #82d0f5;
 }
 