.about-me {
    margin-top: 24px;
    margin-left: 16vw;
    width: 74vw;
    color: #c2c1c0;
    font-size: calc(8px + 1.5vmin);
}

.about-me-section-header {
    color: white;
    font-size: calc(10px + 1.5vmin);
    font-style: bold;
}

.about-me-link {
    color: #87b1f5;
    text-decoration: none;
}

.about-me-link:visited {
    color: #87b1f5;
}

.about-me-link:hover {
    margin-bottom: -1px;
    cursor: pointer;
    border-bottom: 2px solid #87b1f5;
}


.about-me-profile-link:link {
    color: #61dafb;
    text-decoration: none;
}

.about-me-profile-link:visited {
    color: #61dafb;
}

.about-me-profile-link:focus {
    border-bottom: 1px solid;
    margin-bottom: -1px;
}

.about-me-profile-link:hover {
    border-bottom: 2px solid white;
    margin-bottom: -2px;
    text-decoration: none;
}

.about-me-profile-links {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-bottom: 24px;
}

.bottom-spacer {
    height: 128px;
}