.personal-projects-list {
    margin-top: 24px;
    margin-bottom: 48px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

.project {
    --project-height: min(186px, 22vmin);
    --project-width: min(450px, 80vmin);
    --horizontal-margins: 24px;
    --project-vertical-content-spacing: 4px;
 }
 
 .project {
     min-width: var(--project-width);
     max-width: var(--project-width);
     min-height: var(--project-height);
     max-height: var(--project-height);
     margin-top: 48px;
     margin-left: var(--horizontal-margins);
     margin-right: var(--horizontal-margins);
     display: flex;
     flex-direction: row;
     align-items: center;
     justify-content: flex-start;
     color: #c2c1c0;
 }
 
 .project-image-container {
     min-width: var(--project-height);
     min-height: var(--project-height);
     width: var(--project-height);
     display:  flex;
     flex-direction: row;
     align-items: flex-start;
     justify-content: center;
 }
 
 .project-image {
     max-width: var(--project-height);
     max-height: var(--project-height);
 }
 
 .project-text {
     padding-bottom: 4px;
     height: calc(var(--project-height) - 4px);
     margin-left: calc(10px + 1vmin);
     display: flex;
     flex-direction: column;
     align-items: flex-start;
     justify-content: space-between;
 }
 
 .project-title {
   font-size: calc(10px + 1.5vmin);
   color: white;
 }
 
 .project-description {
     flex: 1;
     margin-top: var(--project-vertical-content-spacing);
     font-size: calc(8px + 1.2vmin);
 }
 
 .project-skills {
     margin-top: var(--project-vertical-content-spacing);
     font-size: calc(6px + 1.2vmin);
     font-style: italic;
     color: #CDFEAA;
 }
 
 .project-links {
     margin-top: var(--project-vertical-content-spacing);
     display: flex;
     min-height: 26px;
     max-height: 26px;
 }
 
 .project-link {
     margin-right: calc(10px + 1vmin);
     font-size: calc(2px + 2vmin);
     outline: none;
     text-decoration: none;
     padding-top: 2px;
     padding-left: 1px;
     padding-right: 1px;
     display: flex;
     align-items: center;
 }
 
 .project-link i {
     font-size: calc(4px + 2vmin);
     color: #cbd1d4;
 }
   
 .project-link:link {
     color: #82d0f5;
 }
 
 .project-link:visited {
     color: #82d0f5;
 }
 
 .project-link:focus {
     border-bottom: 1px solid;  
     margin-bottom: -1px;
 }
 
 .project-link:hover {
     border-bottom: 1px solid;    
     margin-bottom: -1px;
 }