.app {
  --tab-width: calc(80px + 12vmin);
  --tab-spacer-width: max(8px, 4vmin);
}

.app-header {
  background-color: #282c34;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  margin-top: 24px;
  color: white;
}

.Jacob {
  flex: none;
  text-align: center;
  font-size: calc(14px + 2vmin);
  color: white;

  animation-duration: 1.5s;
  animation-name: slidein;
}

@keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%; 
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}

.app-link-container-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.app-link-container {
  margin-top: 16px;
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-around;

}

.app-link-spacer {
  min-width: var(--tab-spacer-width);
  max-width: var(--tab-spacer-width);
}

.app-link {
  min-width: var(--tab-width);
  max-width: var(--tab-width);
  cursor: pointer;
  flex: none;
  text-align: center;
  color: #61dafb;
  display: inline;
  position: relative;
}

.app-link:after {
  content: '';
  margin-top: 2px;
  width: 0px;
  left: 50%;
  height: 2px;
  display: block;
  position: absolute;
  background: white;
  transition: width 300ms ease, left 300ms ease;
}

.app-link:hover:after {
  width: 100%;
  left: 0;
}

.app-link-selected.app-link:after {
  width: 100%;
  left: 0;
}

.app-list {
  margin-top: 48px;
  margin-bottom: 48px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}